import React, { Component } from "react";
import { connect } from "react-redux";
import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import { Gramasi, NumberOnly } from "../../../Validasi/normalize.jsx";
import ValidasiMasterKategori from "../../../Validasi/ValidasiMasterKategori.jsx";

const maptostate = (state) => {
  if (state.datamaster.ShowtModalKategori !== null) {
    return {
      initialValues: {
        kode: state.datamaster.ShowtModalKategori.kode,
        nama_kategori: state.datamaster.ShowtModalKategori.nama_kategori,
        harga_pergram: state.datamaster.ShowtModalKategori.harga_pergram,
        presentase: state.datamaster.ShowtModalKategori.presentase,
        kadar: state.datamaster.ShowtModalKategori.kadar,
      },
    };
  }
};
class FormModalKategori extends Component {
  state = {};
  render() {
    return (
      <Modal
        backdrop="static"
        keyboard={false}
        isOpen={this.props.isOpen}
        toggle={() => this.props.dispatch(hideModal())}
      >
        <ModalHeader> {this.props.title} </ModalHeader>
        <form onSubmit={this.props.handleSubmit} autoComplete="off">
          <ModalBody>
            <Field
              name="kode"
              component={ReanderField}
              type="text"
              label="Kode"
              placeholder="Masukan Kode"
              readOnly={this.props.isEdit}
            />
            <Field
              name="nama_kategori"
              component={ReanderField}
              type="text"
              label="Nama Kategori"
              placeholder="Masukan Nama Kategori"
            />
            <Field
              name="harga_pergram"
              component={ReanderField}
              type="text"
              label="Harga / Gram"
              placeholder="Masukan Harga / Gram"
              normalize={Gramasi}
            />
            <Field
              name="presentase"
              component={ReanderField}
              type="text"
              label="Presentase"
              placeholder="Masukan Presentase"
              normalize={NumberOnly}
            />
            <Field
              name="kadar"
              component={ReanderField}
              type="text"
              label="Kadar"
              placeholder="Masukan Kadar"
            />
          </ModalBody>
          <ModalFooter>
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            <button
              className="btn btn-primary"
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </ModalFooter>
        </form>
      </Modal>
    );
  }
}

FormModalKategori = reduxForm({
  form: "ModalKategori",
  enableReinitialize: true,
  validate: ValidasiMasterKategori,
})(FormModalKategori);
export default connect(maptostate, null)(FormModalKategori);
